html, body, #root {
  font-family: 'Rubik', sans-serif;
}

html {
  height: 100%;
  font-size: 10px;
}

body {
  background-color: #000000;
  margin: 0;
  padding: 0;
  height: 100%;
}

#root {
  height: 100%;
}

@media (max-width: 425px) {
  html {
    font-size: 30px;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 40px;
  }
}

@media (min-width: 1024px) {
  html {
    font-size: 50px;
  }
}


